export default {
	data() {
		return {
			c_index:0,
			list:[],
			class_name: 'cou_t_l_01',
		};
	},
	onLoad(options) {
		this._load()
	},
	methods:{
		_load() {
			
			this.$api.http.get('coupon/get_coupon').then((res)=>{
				this.list= res.data
				console.log(this.list)
				uni.stopPullDownRefresh();
			});
		},
		get_coupon(id){
			this.$api.http.get('coupon/add_coupon',{id:id}).then(res=>{
				this.$api.msg('领取成功')
				this._load();
			})
		},
		
	},
	onPullDownRefresh() {
		this._load();
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
